import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumFormatter,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, ConfigECFIrpjCsll } from '../../../../models/dtos/mcg/configECFIrpjCsll';
import { TIPO_REGISTRO_ECF_ENUM_VALUES } from '../../../../models/enumerated/mcg/tipoRegistroECFEnum';
import { TRIMESTRES_ECF_ENUM_VALUES } from '../../../../models/enumerated/mcg/trimestresECFEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const ConfigECFIrpjCsllSearchView: FC<SearchViewProps<ConfigECFIrpjCsll>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const trimestreFormatter = (cell, row) => {
    return row.trimestre ? enumFormatter(cell, row, row.index, TRIMESTRES_ECF_ENUM_VALUES) : <span>Anual</span>;
  };

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    {
      dataField: 'tipoRegistro',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatExtraData: TIPO_REGISTRO_ECF_ENUM_VALUES,
      formatter: enumFormatter,
      sort: true,
      text: 'Tipo de Registro',
    },
    {
      dataField: 'trimestre',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatter: trimestreFormatter,
      sort: true,
      text: 'Período',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção SPED ECF - Registros P200, P300, P400, P500"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresaMCG(globalReducer.globalParameter), () => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={207}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'ConfigECFIrpjCsllEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigECFIrpjCsllSearchView;
